.outer_block_1 {
  background-color: rgb(35, 47, 62);
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.new {
  border: 0;
  margin: 0;
  width: 100%;
  height: 2px;
  background: rgb(35, 47, 62);
  border-top: dotted;
}
.back_to_top {
  border-top: 50px solid rgb(55, 71, 90);
  border-bottom: 50px rgb(55, 71, 90);
}
.outer_block_row {
  color: white;
  margin-top: 30px;
  margin-bottom: 10px;
  margin-left: 50px;
  margin-right: 50px;
  display: flex;
  flex-direction: column;
  text-align: left;
  line-height: 30px;
}
.first_raw {
  font-weight: bolder;
}

.outer_block_2 {
  background-color: rgb(35, 47, 62);
  justify-content: center;
}

.countries {
  padding-top: 10px;
  padding-bottom: 10px;
  margin-left: 5px;
  font-size: 13px;
  text-align: center;
}
.line_of_countries {
  color: white;
  text-align: center;
  margin-left: 50px;
}
.logo {
  text-align: center;
}

.outer_block_3 {
  display: flex;
  flex-direction: row;
  background-color: rgb(19, 26, 34);
  justify-content: center flex-start;
  padding-left: 250px;
}

.option {
  display: flex;
  flex-direction: column;
  color: white;
  margin-top: 30px;
  margin-bottom: 10px;
  margin-left: 50px;
  margin-right: 50px;
}
.option_lineone {
  font-size: 13px;
  font-weight: 700;
}
.option_linetwo {
  font-size: 10px;
}
.option_linethree {
  font-size: 10px;
}
.outer_block_4 {
  display: flex;
  flex-direction: row;
  background-color: rgb(19, 26, 34);
  justify-content: center flex-start;
  padding-left: 250px;
}

.outer_block_5 {
  display: flex;
  flex-direction: row;
  background-color: rgb(19, 26, 34);
  justify-content: center;
  color: white;
  font-size: 13px;
  font-weight: 700;
  padding-top: 50px;
  padding-bottom: 50px;
}
